.ListaTareasSeccion {
  display: flex;

  justify-content: space-around;
  cursor: pointer;
  margin: 2rem;
}
.ListaTareasSeccion {
  padding: 0.5rem;
  width: 100%;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.ListaTareasSeccion.activo {
  background-color: #2d3142;
}
.ListaTareasSeccion:hover {
  background-color: #2d3142;
}
@media only screen and (max-width: 500px) {
  .ListaTareasSeccion {
    flex-direction: column !important;
  }
  .ListaTareasSeccion {
    margin: 0 !important;
  }
}
